.popup__container {
  max-width: 430px;
  width: 100%;
  position: relative;
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  cursor: default;
}

@media screen and (max-width: 600px) {
  .popup__container {
    margin: 0 19px 0 19px;
  }
}
